.loginBody {
  height: 454px;
  width: 1211px;
  position: relative;
  border-radius: var(--br-17xl);
  background-color: var(--color-white);
  display: none;
  max-width: 100%;
}
.googleSymbolsvgIcon {
  height: 64px;
  width: 64px;
  position: relative;
  overflow: hidden;
  flex-shrink: 0;
}
.googleIcon {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.sign {
  text-transform: capitalize;
}
.in {
  text-transform: lowercase;
}
.signIn {
  margin: 0;
  align-self: stretch;
  height: 57px;
  position: relative;
  font-size: inherit;
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
}
.googleIconParent {
  width: 182px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 37px 0;
}
.useYourGoole {
  margin: 0;
  align-self: stretch;
  height: 78px;
  position: relative;
  font-size: var(--font-size-lgi);
  font-weight: 500;
  font-family: inherit;
  display: inline-block;
  flex-shrink: 0;
}
.fRAMEC,
.googleLoginIcon {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.googleLoginIcon {
  align-self: stretch;
  gap: 40px 0;
  z-index: 1;
}
.fRAMEC {
  height: 369px;
  width: 303px;
  min-width: 303px;
}
.learnMore,
.notYourComputer {
  font-family: var(--font-inter);
}
.notYourComputer {
  font-weight: 500;
  color: var(--color-gray);
}
.learnMore {
  color: var(--color-dodgerblue);
}
.notYourComputerContainer {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-base);
  text-align: left;
  display: inline-block;
}
.guestModeMessage {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 90px 0;
  max-width: 100%;
  z-index: 1;
}
.createAccount {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  height: 26px;
  position: relative;
  font-size: var(--font-size-base-5);
  font-weight: 600;
  font-family: var(--font-inter);
  color: var(--color-dodgerblue);
  text-align: left;
  display: inline-block;
  z-index: 1;
}
.nextButton {
  height: 47px;
  width: 91px;
  z-index: 1;
}
.footerFrame,
.sectionB {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.sectionB {
  align-items: center;
  gap: 0 39px;
}
.footerFrame {
  align-items: flex-start;
  padding: 0 var(--padding-12xl);
  box-sizing: border-box;
  max-width: 100%;
}
.fRAMEB,
.subFrameJ {
  display: flex;
  align-items: flex-end;
  max-width: 100%;
}
.subFrameJ {
  width: 588px;
  flex-direction: column;
  justify-content: flex-start;
  gap: 42px 0;
  min-width: 588px;
}
.fRAMEB {
  align-self: stretch;
  border-radius: var(--br-17xl);
  background-color: var(--color-white);
  flex-direction: row;
  justify-content: space-between;
  padding: var(--padding-35xl) var(--padding-lg) var(--padding-12xl)
    var(--padding-43xl);
  box-sizing: border-box;
  gap: var(--gap-xl);
}
.help,
.privacy,
.terms {
  cursor: pointer;
  position: relative;
  font-weight: 600;
}
.help {
  width: 38px;
  display: inline-block;
  flex-shrink: 0;
}
.privacy,
.terms {
  flex: 1;
}
.helpTermsFrame,
/* Permite o container encolher e centralizar */
.subFrameA {
  width: 100%;
  max-width: 720px;   /* valor confortável para desktop */
  margin: 0 auto;
  padding: 16px;
}

/* Este é o container principal (o “cartão branco”) */
.fRAMEB {
  width: 100%;
  border-radius: var(--br-17xl);
  background-color: var(--color-white);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: var(--padding-25xl) var(--padding-xl);
  gap: 32px;
  box-sizing: border-box;
}

/* Remover travas */
.fRAMEC,
.subFrameJ {
  min-width: auto !important;
  width: 100%;
}

/* Google login section */
.fRAMEC {
  max-width: 280px;
}

/* Inputs + rodapé */
.subFrameJ {
  max-width: 420px;
}

/* Mobile layout: empilha */
@media (max-width: 700px) {

  .fRAMEB {
    flex-direction: column;
    align-items: stretch;
    padding: 24px;
    gap: 24px;
  }

  .fRAMEC,
  .subFrameJ {
    width: 100%;
    max-width: 100%;
  }

  .googleSymbolsvgIcon {
    width: 48px;
    height: 48px;
  }

  .signIn {
    font-size: 26px;
    margin-top: 12px;
  }

  .useYourGoole {
    font-size: 15px;
  }

  .sectionB {
    justify-content: space-between;
    width: 100%;
  }

  .nextButton {
    width: 100px;
    height: 42px;
  }
}

/* Mobile muito pequeno */
@media (max-width: 420px) {

  .signIn {
    font-size: 22px;
  }

  .googleIconParent {
    gap: 20px;
  }
}
